.productReviewsContainer{
    width: 100%;
    box-sizing: border-box;
    background-color: white;
    border-left: 1px solid rgba(0, 0, 0, 200);
    height: 100vh;
}

.productReviewsForm{
    width: 20rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    padding: 3vmax;
    background-color: white;
}

.productReviewsFormHeading{
    color: rgba(0, 0, 0, 800);
    font: 300 2rem "Roboto";
    text-align: center;
}

.productReviewsForm > div{
    display: flex;
    width: 100%;
    align-items: center;
    margin: 2rem;
}

.productReviewsForm > div > input{
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 300);
    border-radius: 4px;
    font: 300 0.8rem;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    outline: none;
}

.productReviewsForm > div > svg{
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.2rem;
    color: rgba(0, 0, 0, 650);
}

@media (max-width:600px) {
    .productReviewsContainer{
        border-left: none;
        border-top: 1px solid rgba(0, 0, 0, 200);
    }

    .productReviewsForm > div > input{
        padding: 2.5vmax 5vmax;
    }
}