.ProductDetails {
  width: 100vw;
  max-width: 100%;
  padding: 6vmax;
  box-sizing: border-box;
  display: flex;
}

.ProductDetails > div {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding: 2vmax;
  box-sizing: border-box;
  border: 1px solid white;
}

.ProductDetails > div:last-child {
  align-items: flex-start;
}

.CarouselImage {
  width: 100%;
  height: 100%;
}

.detailsBlock-1 > h2 {
  color: rgb(54, 54, 54);
}

.detailsBlock-1 > p {
  color: rgba(54, 54, 54, 0.582);
  font: 200 12px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.detailsBlock-2 {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  border-top: 1px solid rgba(0, 0, 0, 0.205);
  border-bottom: 1px solid rgba(0, 0, 0, 0.205);
  width: 70%;
  padding: 1vmax 0;
}

.detailsBlock-2-span {
  font: 200 12px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  color: rgba(0, 0, 0, 0.650);
}

.detailsBlock-3 {
  width: 70%;
}

.detailsBlock-3 > h1 {
  color: rgba(17, 17, 17, 0.795);
  font: 400 1.8vmax;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  margin: 1vmax 0;
}

.detailsBlock-3-1 {
  display: flex;
  align-items: center;
}

.detailsBlock-3-1-1 > button {
  border: none;
  background-color: rgba(0, 0, 0, 0.616);
  padding: 0.5vmax 0.75vmax;
  cursor: pointer;
  color: white;
  transition: all 0.5s;
  font-weight: bold;
}

.detailsBlock-3-1-1 > button:hover {
  background-color: rgba(0, 0, 0, 0.767);
}

.detailsBlock-3-1-1 > input {
  border: none;
  padding: 0.5vmax;
  width: 1vmax;
  text-align: center;
  outline: none;
  font: 400 0.8rem;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  color: rgba(0, 0, 0, 0.74);
}

.detailsBlock-3-1 > button:last-child {
  border: none;
  cursor: pointer;
  color: white;
  transition: all 0.5s;
  background-color: tomato;
  font: 500 0.9rem;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  border-radius: 20px;
  padding: 0.45rem 2vmax;
  margin: 1vmax;
  outline: none;
}

.detailsBlock-3-1 > button:last-child:hover {
  background-color: rgb(214, 84, 61);
}

.detailsBlock-3 > p {
  border-top: 1px solid rgba(0, 0, 0, 0.205);
  border-bottom: 1px solid rgba(0, 0, 0, 0.205);
  padding: 1vmax 0;
  color: rgba(0, 0, 0, 0.651);
  font: 400 1vmax;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  margin: 1vmax 0;
}

.detailsBlock-4 {
  color: rgba(0, 0, 0, 0.849);
  font: 500 1.2vmax;
  font-family: sans-serif;
}

.detailsBlock-4 > p {
  color: rgba(0, 0, 0, 0.719);
  font: 300 1rem sans-serif;
}

.submitReview {
  border: none;
  background-color: tomato;
  font: 500 0.9rem;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  border-radius: 20px;
  padding: 0.4rem 2vmax;
  margin: 1vmax 0;
  color: white;
  cursor: pointer;
  transition: all 0.5s;
  outline: none;
}

.submitReview:hover {
  background-color: rgb(197, 68, 45);
  transform: scale(1.1);
}

.submitDialog{
  display: flex;
  flex-direction: column;
}

.submitDialogTextArea{
  border: 1px solid rgba(0, 0, 0, 0.080);
  margin: 1vmax 0;
  outline: none;
  padding: 1rem;
  font: 300 1rem "Roboto";
}

/* Reviews */
.reviewsHeading {
  color: #000;
  font: 500 1.2rem;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  text-align: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.225);
  padding: 1vmax;
  width: 20vmax;
  margin: auto;
  margin-bottom: 4vmax;
}

.reviews {
  display: flex;
  overflow: auto;
  scroll-behavior: smooth;
  scroll-snap-type: proximity;
}

.reviewCard {
  flex: none;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.225);
  border: 1px solid rgba(0, 0, 0, 0.116);
  width: 30vmax;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 1vmax;
  padding: 3vmax;
}

.reviewCard > img {
  width: 4rem;
}

.reviewCard > p {
  color: rgba(0, 0, 0, 0.83);
  font: 600 0.9rem;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.reviewCardComment {
  margin-top: 10px;
  color: rgb(12, 12, 12);
  font: 500 0.8rem monospace;
}

.noReviews {
  font: 400 1.3vmax;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  text-align: center;
  color: tomato;
}

@media screen and (max-width: 600px) {
  .ProductDetails {
    flex-direction: column;
    height: unset;
  }

  .ProductDetails > div:last-child {
    align-items: center;
  }

  .detailsBlock-1 > h2 {
    font-size: 2.8vmax;
    text-align: center;
  }

  .detailsBlock-1 > p {
    text-align: center;
    font-size: 12px;
  }

  .detailsBlock-2 {
    justify-content: center;
  }

  .detailsBlock-2-span {
    font-size: 12px;
  }

  .detailsBlock-3 > h1 {
    font: 700 3vmax;
    font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
    text-align: center;
  }

  .detailsBlock-3-1 {
    flex-direction: column;
  }

  .detailsBlock-3-1-1 {
    padding: 2vmax 0;
  }

  .detailsBlock-3-1-1 > button {
    padding: 1.2vmax;
    width: 4vmax;
  }

  .detailsBlock-3-1-1 > input {
    padding: 1.5vmax;
    width: 3vmax;
    font: 400 1.8vmax;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
  }

  .detailsBlock-3-1 > button:last-child {
    font: 500 1.7vmax;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    padding: 1.5vmax;
    width: 20vmax;
    margin: 3vmax 0;
  }

  .detailsBlock-3 > p {
    padding: 2.5vmax 0;
    text-align: center;
    font: 400 2vmax;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
  }

  .detailsBlock-4 {
    font: 500 1rem sans-serif;
  }

  .detailsBlock-4 > p {
    font: 500 1rem sans-serif;
    color: #000000bb;
  }

  .submitReview {
    font: 500 1.7vmax;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    width: 20vmax;
    margin: 3vmax 0;
  }
}
