.newProductContainer{
    width: 100%;
    box-sizing: border-box;
    background-color: rgb(221, 221, 221);
    border-left: 1px solid rgba(0, 0,0, 0.158);
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.newProductContainer h1{
    color: rgba(0, 0,0, 0.750);
    font: 300 1.5rem "Roboto";
    text-align: center;
}

.createProductForm{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    padding: 3vmax;
    justify-content: space-evenly;
    height: 70%;
    width: 40vh;
    background-color: white;
    box-shadow: 0 0 10px rgba(0, 0,0, 0.350);
}

.createProductForm > div{
    display: flex;
    width: 100%;
    align-items: center;
}

.createProductForm > div > input, 
.createProductForm > div > select,
.createProductForm > div > textarea{
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0,0, 0.350);
    border-radius: 4px;
    font: 300 0.8rem;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    outline: none;
}

.createProductForm > div > svg{
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.2rem;
    color: rgba(0, 0,0, 0.650);
}

#createProductFormFile > input{
    display: flex;
    padding: 0%;
}

#createProductFormFile > input::file-selector-button{
    cursor: pointer;
    width: 100%;
    z-index: 2;
    height: 5vh;
    border: none;
    margin: 0%;
    font: 400 0.8rem;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    transition: all 0.5s;
    padding: 0 1vmax;
    color: rgba(0, 0,0, 0.650);
    background-color: rgb(255, 255, 255);
}

#createProductFormFile > input::file-selector-button:hover{
    background-color: rgb(221, 221, 221);
}

#createProductFormImage{
    width: 100%;
    overflow: auto;
}

#createProductFormImage > img{
    width: 7rem;
    margin: 0 0.5vmax;
}

#createProductBtn{
    border: none;
    background-color: tomato;
    color: white;
    font: 300 0.9rem "Roboto";
    width: 100%;
    padding: 0.8vmax;
    cursor: pointer;
    transition: all 0.5s;
    border-radius: 4px;
    outline: none;
    box-shadow: 0 2px 5px rgba(0, 0,0, 0.350);
}

#createProductBtn:hover{
    background-color: rgb(180, 66, 46);
}

@media (max-width: 600px) {
    .newProductContainer{
        background-color: white;
    }
    .createProductForm{
        padding: 5vmax;
    }

    .createProductForm > div > input,
    .createProductForm > div > select,
    .createProductForm > div > textarea{
        padding: 2vmax 5vmax;
        margin: 5px 0;
    }

    #createProductFormFile > img{
        border-radius: 100%;
    }

    #createProductFormFile > input:file-selector-button{
        height: 7vh;
    }

    #createProductBtn{
        padding: 1.8vmax;
    }
}