.updateOrderForm{
    margin: 5vmax 0;
    padding: 3vmax;
    background-color: white;
}

.updateOrderForm > div{
    display: flex;
    width: 100%;
    align-items: center;
}

.updateOrderForm > div > select{
    padding: 1vmax 4vmax;
    margin: 2rem 0;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.300);
    border-radius: 4px;
    font: 300 0.8rem;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    outline: none;
}

.updateOrderForm > div > svg{
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.2rem;
    color: rgba(0, 0, 0, 0.650)
}

@media (max-width: 600px) {
    .updateOrderForm{
        padding: 5vmax;
    }

    .updateOrderForm > div > select{
        padding: 2.5vmax 5vmax;
    }
}